          :dark="$store.state.dark"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              icon
              v-bind="attrs"
              v-on="on"
            >
              <v-icon>mdi-dots-vertical</v-icon>
            </v-btn>
          </template>
          <v-list dense class="py-0">
            <v-list-item to="/config">
              <v-list-item-icon>
                <v-icon>mdi-cog</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Configuraes</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item
              v-if="$vuetify.breakpoint.mobile"
              @click="$store.commit('setDark', !$store.state.dark)"
            >
              <v-list-item-icon>
                <v-icon v-if="$store.state.dark">mdi-white-balance-sunny</v-icon>
                <v-icon v-else>mdi-weather-night</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title v-if="$store.state.dark">Modo claro</v-list-item-title>
                <v-list-item-title v-else>Modo escuro</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item to="/perfil" v-if="$store.state.ficha.cadastro && $store.state.ficha.cadastro.fonte === 'I2BR'">
              <v-list-item-icon>
                <v-icon>mdi-account-circle</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Perfil comercial</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-list-item
              v-if="$vuetify.breakpoint.mobile"
              @click="reloadData"
            >
              <v-list-item-icon>
                <v-icon>mdi-refresh</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>Recarregar dados</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item @click="$store.dispatch('session/destroy')">
              <v-list-item-icon>
