      :type="i.tipo"
      class="mt-2 mb-0 mx-3 text-justify px-2"
      elevation="1"
      dense
      style="font-size: .9rem"
    >
      {{i.texto}}
    </v-alert>
    <v-container
      class="infos mb-6"
      :class="{dark: $store.state.dark}"
    >
      <!-- Nome e CRECI -->
      <v-sheet
        elevation="2"
        rounded
        class="d-flex align-center pa-1 mb-3"
        :dark="$store.state.dark"
      >
        <img src="@/assets/icon/id_card.svg" alt="" style="width: 3rem; max-height: 3rem">
        <div class="flex-fill pl-2 ml-2 border-left text-center" style="border-left: 1px solid #dee2e6">
          <p class="mb-0 subtitle-2">{{nome}}</p>
          <p class="mb-0 title" v-if="$store.state.ficha.cadastro.solenidade">{{creci}}</p>
          <p class="mb-0 title" v-else>CRECI PENDENTE</p>
        </div>
      </v-sheet>
      <!-- Anuidade e Recadastramento -->
      <v-row dense>
        <v-col cols="6">
          <v-sheet
            elevation="2"
            rounded
            class="d-flex align-center pa-1"
            :dark="$store.state.dark"
          >
            <div class="flex-fill text-center">
              <p class="mb-0 subtitle-2">ANUIDADE</p>
              <v-progress-circular v-if="$store.state.ficha.cadastroLoading" indeterminate color="info" size="24"></v-progress-circular>
              <v-chip color="success" label small v-else-if="$store.state.ficha.cadastro.anuidade">EM DIA</v-chip>
              <v-chip color="error" label small v-else>PENDENTE</v-chip>
            </div>
          </v-sheet>
        </v-col>
        <v-col cols="6">
          <v-sheet
            elevation="2"
            rounded
            class="d-flex align-center pa-1"
            :dark="$store.state.dark"
          >
            <div class="flex-fill text-center">
              <p class="mb-0 subtitle-2">RECADASTRAMENTO</p>
              <v-progress-circular v-if="$store.state.ficha.cadastroLoading" indeterminate color="info" size="24"></v-progress-circular>
              <v-chip color="success" label small v-else-if="$store.state.ficha.cadastro.recad_validade">EM DIA</v-chip>
              <v-chip color="error" label small v-else>PENDENTE</v-chip>
            </div>
          </v-sheet>
        </v-col>
      </v-row>
      <!-- NPA -->
      <v-sheet
        elevation="2"
        rounded
        class="d-flex align-center pa-1 mt-3"
        v-if="$store.state.ficha.cadastro && $store.state.ficha.cadastro.npa"
        :dark="$store.state.dark"
      >
        <img src="@/assets/icon/interview.svg" alt="" style="width: 3rem; max-height: 3rem">
        <div class="flex-fill pl-2 ml-2 border-left text-center" style="border-left: 1px solid #dee2e6">
          <p class="mb-0 subtitle-2">NOME PROFISSIONAL ABREVIADO</p>
          <p class="mb-0 title">{{$store.state.ficha.cadastro.npa}}</p>
        </div>
      </v-sheet>
      <!-- CNAI -->
      <v-sheet
        elevation="2"
        rounded
        class="d-flex align-center pa-1 mt-3"
        v-if="cnai"
        :dark="$store.state.dark"
      >
        <img src="@/assets/icon/house_search.svg" alt="" style="width: 3rem; max-height: 3rem">
        <div class="flex-fill pl-2 ml-2 border-left text-center" style="border-left: 1px solid #dee2e6">
          <p class="mb-0 subtitle-2">AVALIADOR IMOBILIRIO</p>
          <p class="mb-0 title">{{cnai}}</p>
        </div>
