    </v-container>
  </div>
</template>

<script>
import AppBar from '@/components/AppBar';
import CreciHelper from '@/helpers/CreciHelper';
import moment from 'moment';
export default {
  name: 'Home',
  components: {AppBar},
  computed: {
    nome() {
      return this.$store.state.ficha.cadastro ? this.$store.state.ficha.cadastro.nome : '...';
    },
    creci() {
      return this.$store.state.ficha.cadastro ? 'CRECI-' + CreciHelper.idParaUf(this.$store.getters['session/payload'].regional) + ' ' + this.$store.state.ficha.cadastro.creci : '...';
    },
    cnai() {
      return this.$store.state.ficha.cadastro && this.$store.state.ficha.cadastro.cnai ? 'CNAI ' + this.$store.state.ficha.cadastro.cnai : '';
    },
  },
  methods: {
    reloadData() {
      this.$store.dispatch('ficha/reloadCadastro');
      this.$store.dispatch('ficha/reloadFoto');
    },
  },
  created() {
    if (this.$store.state.ficha.cadastroData && this.$store.state.startTime) {
      const diffStart = moment().diff(this.$store.state.startTime, 'seconds');
      const diffUpdate = moment().diff(this.$store.state.ficha.cadastroData, 'seconds');
      if (diffUpdate >= 60 && diffStart >= 60) this.reloadData();
    }
  },
}
</script>

