<template>
  <v-container
    fluid
    class="fill-height"
    style="background: linear-gradient(90deg, #0072ff 0%, #00d4ff 100%);"
  >
    <!-- Formulario de login -->
    <v-card class="mx-auto" elevation="5" shaped width="400" max-width="94%">
      <v-card-title class="flex-column pb-0">
        <v-avatar class="elevation-3 mb-3" height="6rem" width="6rem">
          <img src="@/assets/logo.svg" alt=""/>
        </v-avatar>
        <p class="title mb-2">iCorretor</p>
      </v-card-title>
      <v-card-text>
        <!-- Alerta -->
        <v-alert
          v-for="i in $store.state.alertas.filter(i => i.local === 1)"
          :key="i.id"
          :type="i.tipo"
          class="mb-2"
          dense
        >
          {{i.texto}}
        </v-alert>
        <v-form @submit.prevent="submitLogin" ref="form-login" :disabled="disableForms">
          <v-select
            label="Regional"
            prepend-inner-icon="mdi-map-marker-radius"
            :items="regionais"
            item-value="id"
            item-text="ufExtenso"
            :rules="rules.regionalRules"
            class="mb-2"
            v-model="iptRegional"
            :messages="compatibilidadeParcial ? 'Este regional no tem compatibilidade total' : undefined"
          >
            <template v-slot:selection="{item}">{{item.id}} - {{item.ufExtenso.toUpperCase()}}</template>
            <template v-slot:item="{item}">{{item.id}} - {{item.ufExtenso.toUpperCase()}}</template>
          </v-select>
          <v-text-field
            label="CPF"
            placeholder="cpf"
            prepend-inner-icon="mdi-account"
            :rules="rules.cpfRules"
            type="tel"
            autocomplete="username"
            v-mask="'###.###.###-##'"
            class="mb-2"
            v-model="iptCpf"
          ></v-text-field>
          <v-text-field
            label="Senha"
            placeholder="senha"
            prepend-inner-icon="mdi-lock"
            :rules="rules.passwordRules"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            :type="showPassword ? 'text' : 'password'"
            @click:append="showPassword = !showPassword"
            autocomplete="current-password"
            autocapitalize="off"
            v-model="iptPassword"
          ></v-text-field>
          <v-btn
            rounded
            color="primary"
            type="submit"
            block
            :loading="loadingLogin"
          >ENTRAR</v-btn>
          <v-btn
            rounded
            outlined
            type="button"
            block
            class="mt-3"
            @click="dialogPasswdRecover = true"
            :disabled="loadingLogin"
          >Esqueci minha senha</v-btn>
          <v-btn
            text
            small
            outlined
            type="button"
            class="mx-auto d-block mt-3"
            @click="dialogFirstAccess = true"
            :disabled="loadingLogin"
          >Primeiro acesso</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
    <!-- Formulario de recuperacao de senha -->
    <v-dialog v-model="dialogPasswdRecover" width="400" max-width="94%">
      <v-card>
        <v-stepper v-model="stepperPasswdRecover">
          <v-stepper-items>
            <v-stepper-content step="1" class="pa-0">
              <v-form
                ref="form-passwdrequest"
                @submit.prevent="submitRequisitarRecuperacaoDeSenha"
                :disabled="loadingPasswdRecover"
              >
                <v-card-title>Solicitar recuperao de senha</v-card-title>
                <v-card-subtitle>Enviaremos um cdigo de segurana para o seu e-mail</v-card-subtitle>
                <v-card-text class="pb-0">
                  <v-select
                    label="Regional"
                    prepend-inner-icon="mdi-map-marker-radius"
                    :items="regionais"
                    item-value="id"
                    item-text="ufExtenso"
                    :rules="rules.regionalRules"
                    class="mb-2"
                    v-model="iptRegional"
                  >
                    <template v-slot:selection="{item}">{{item.id}} - {{item.ufExtenso.toUpperCase()}}</template>
                    <template v-slot:item="{item}">{{item.id}} - {{item.ufExtenso.toUpperCase()}}</template>
                  </v-select>
                  <v-text-field
                    label="CPF"
                    placeholder="cpf"
                    prepend-inner-icon="mdi-account"
                    :rules="rules.cpfRules"
                    type="tel"
                    autocomplete="username"
                    v-mask="'###.###.###-##'"
                    class="mb-2"
                    v-model="iptCpf"
                  ></v-text-field>
                  <v-text-field
                    label="E-mail"
                    placeholder="e-mail"
                    prepend-inner-icon="mdi-email"
                    :rules="rules.emailRules"
                    type="email"
                    class="mb-2"
                    v-model="iptEmail"
                  ></v-text-field>
                </v-card-text>
                <v-card-actions class="justify-center">
                  <v-btn
                    color="blue darken-1"
                    small
                    outlined
                    :disabled="loadingPasswdRecover"
                    @click="dialogPasswdRecover = false"
                  >Cancelar</v-btn>
                  <v-btn
                    color="blue darken-1"
                    dark
                    small
                    type="submit"
                    :loading="loadingPasswdRecover"
                  >Solicitar</v-btn>
                </v-card-actions>
              </v-form>
            </v-stepper-content>
            <v-stepper-content step="2" class="pa-0">
              <v-form
                ref="form-passwdcode"
                @submit.prevent="submitConferirCodigoDeRecuperacao"
                :disabled="loadingPasswdRecover"
              >
                <v-card-text class="pb-0">
                  <v-alert type="info">
                    Enviamos um cdigo de segurana para o seu e-mail, ele expira em 10 minutos, insira o cdigo abaixo.
                  </v-alert>
                  <v-text-field
                    label="Cdigo de segurana"
                    placeholder="Digite aqui"
                    type="tel"
                    outlined
                    counter
                    maxlength="6"
                    :rules="[rules.required, rules.numberOnly, rules.securityCode]"
                    v-model="iptResetCode"
                  ></v-text-field>
                </v-card-text>
                <v-card-actions class="justify-center">
                  <v-btn
                    color="blue darken-1"
                    small
                    outlined
                    :disabled="loadingPasswdRecover"
                    @click="dialogPasswdRecover = false"
                  >Cancelar</v-btn>
                  <v-btn
                    color="blue darken-1"
                    dark
                    small
                    type="submit"
                    :loading="loadingPasswdRecover"
                  >Confirmar</v-btn>
                </v-card-actions>
              </v-form>
            </v-stepper-content>
            <v-stepper-content step="3" class="pa-0">
              <v-form
                ref="form-newpasswd"
                @submit.prevent="submitAlterarSenha"
                :disabled="loadingPasswdRecover"
              >
                <v-card-text class="pb-0">
                  <v-text-field
                    label="Sua nova senha"
                    placeholder="senha"
                    prepend-inner-icon="mdi-lock"
                    :rules="rules.passwordRules"
                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="showPassword ? 'text' : 'password'"
                    @click:append="showPassword = !showPassword"
                    autocapitalize="off"
                    v-model="iptPassword"
                    hint="min. 6 digitos"
                  ></v-text-field>
                  <v-text-field
                    label="Repita a nova senha"
                    placeholder="senha"
                    prepend-inner-icon="mdi-lock"
                    :rules="[...rules.passwordRules, rulePasswordRepeat]"
                    type="password"
                    autocapitalize="off"
                    v-model="iptPasswordRepeat"
                  ></v-text-field>
                </v-card-text>
                <v-card-actions class="justify-center">
                  <v-btn
                    color="blue darken-1"
                    small
                    outlined
                    :disabled="loadingPasswdRecover"
                    @click="dialogPasswdRecover = false"
                  >Cancelar</v-btn>
                  <v-btn
                    color="blue darken-1"
                    dark
                    small
                    type="submit"
                    :loading="loadingPasswdRecover"
                  >Confirmar</v-btn>
                </v-card-actions>
              </v-form>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-card>
    </v-dialog>
    <!-- Formulario de primeiro acesso -->
    <v-dialog v-model="dialogFirstAccess" width="400" max-width="94%" :persistent="loadingFirstAccess">
      <v-card>
        <v-form @submit.prevent="submitFirstAccess" ref="form-firstaccess" :disabled="disableForms">
          <v-card-title>Primeiro acesso</v-card-title>
          <v-card-subtitle>Cadastrando conta de acesso</v-card-subtitle>
          <v-card-text>
            <v-select
                label="Regional"
                prepend-inner-icon="mdi-map-marker-radius"
                :items="regionais"
                item-value="id"
                item-text="ufExtenso"
                :rules="rules.regionalRules"
                class="mb-2"
                v-model="iptRegional"
            >
              <template v-slot:selection="{item}">{{item.id}} - {{item.ufExtenso.toUpperCase()}}</template>
              <template v-slot:item="{item}">{{item.id}} - {{item.ufExtenso.toUpperCase()}}</template>
            </v-select>
            <v-text-field
                label="CPF"
                placeholder="cpf"
                prepend-inner-icon="mdi-account"
                :rules="rules.cpfRules"
                type="tel"
                autocomplete="username"
                v-mask="'###.###.###-##'"
                class="mb-2"
                v-model="iptCpf"
            ></v-text-field>
            <v-text-field
                label="E-mail"
                placeholder="e-mail"
                prepend-inner-icon="mdi-email"
                :rules="rules.emailRules"
                type="email"
                class="mb-2"
                v-model="iptEmail"
            ></v-text-field>
            <v-text-field
                label="Sua nova senha"
                placeholder="senha"
                prepend-inner-icon="mdi-lock"
                :rules="rules.passwordRules"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                :type="showPassword ? 'text' : 'password'"
                @click:append="showPassword = !showPassword"
                autocapitalize="off"
                v-model="iptPassword"
                hint="min. 6 digitos"
            ></v-text-field>
            <v-text-field
                label="Repita a senha"
                placeholder="senha"
                prepend-inner-icon="mdi-lock"
                :rules="[...rules.passwordRules, rulePasswordRepeat]"
                type="password"
                autocapitalize="off"
                v-model="iptPasswordRepeat"
            ></v-text-field>
          </v-card-text>
          <v-card-actions class="justify-center">
            <v-btn color="primary" type="submit" :loading="loadingFirstAccess">Cadastrar</v-btn>
            <v-btn color="secondary" @click="dialogFirstAccess = false" :disabled="loadingFirstAccess">Cancelar</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    <!-- Instrucao para ativar a conta -->
    <v-dialog v-model="dialogEmailAtivacao" width="400" max-width="94%">
      <v-card>
        <v-card-title>
          Conta no confirmada
          <v-spacer></v-spacer>
          <v-icon @click="dialogEmailAtivacao = false">mdi-close</v-icon>
        </v-card-title>
        <v-card-text>
          Por questes de segurana, enviamos uma mensagem para o seu e-mail (<strong>{{emailAtivacao}}</strong>) com a instruo para ativar sua conta.<br/>
          Este procedimento garante que a conta tenha sido criada pelo corretor, e que o mesmo possui acesso ao e-mail informado.
          <v-btn color="primary" block rounded class="my-2" @click="dialogEmailAtivacao = false" :disabled="loadingReenviarEmail">OK</v-btn>
          <v-btn color="warning" block rounded @click="reenviarEmail" :loading="loadingReenviarEmail">
            <v-icon class="mr-2">mdi-email-sync</v-icon>
            Enviar novamente
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import CreciHelper from '@/helpers/CreciHelper';
import StringHelper from '@/helpers/StringHelper';
import AppWebClient from '@/http/AppWebClient';
import SessionWebClient from '@/http/SessionWebClient';
import I2brWebClient from "@/http/I2brWebClient";
export default {
  name: 'Login',
  data: () => ({
    regionais: CreciHelper.regionais,
    showPassword: false,
    rules: {
      regionalRules: [
        v => !!v || 'Selecione o regional',
      ],
      cpfRules: [
        v => !!v || 'O CPF  obrigatrio',
        v => StringHelper.validCpf(v) || 'Digite um CPF vlido',
      ],
      passwordRules: [
        v => !!v || 'Digite sua senha',
        v => (!/\s/g.test(v)) || 'Espaos no so permitidos',
        v => (v && v.length >= 6) || 'Precisa ter pelo menos 6 caracteres',
        v => (v && v.length <= 16) || 'O limite  16 caracteres',
      ],
      emailRules: [
        v => !!v || 'E-mail  obrigatrio',
        v => /.+@.+\..+/.test(v) || 'Insira um E-mail vlido',
      ],
      required: v => {
        if (!v || !v.trim()) return 'Este campo  obrigatrio';
        else return true;
      },
      numberOnly: v => {
        if (!v || !v.trim()) return true;
        if (!v.trim().replace(/\d/g,'')) return true;
        return 'O campo deve conter apenas nmeros';
      },
      securityCode: v => {
        if (v && v.length !== 6) return 'Complete os 6 dgitos';
        else return true;
      },
    },
    iptRegional: null,
    iptCpf: '',
    iptPassword: '',
    iptPasswordRepeat: '',
    iptEmail: '',
    iptResetCode: '',
    dialogPasswdRecover: false,
    dialogFirstAccess: false,
    dialogEmailAtivacao: false,
    disableForms: false,
    loadingLogin: false,
    loadingPasswdRecover: false,
    loadingFirstAccess: false,
    loadingReenviarEmail:false,
    emailAtivacao: '',
    stepperPasswdRecover: 1,
  }),
  computed: {
    compatibilidadeParcial() {
      if (!this.iptRegional) return false;
      const clienteI2br = I2brWebClient.regionais.includes(this.iptRegional);
      const clienteIncorpnet = this.iptRegional === 7 || this.iptRegional === 17;
      return !clienteI2br && !clienteIncorpnet;
    },
  },
  methods: {
    async submitLogin() {
      if (!this.$refs['form-login'].validate()) return;
      this.loadingLogin = true;
      this.disableForms = true;
      try {
        const sessionWebClient = new SessionWebClient();
        const token = await sessionWebClient.createSession(this.iptRegional, this.iptCpf, this.iptPassword);
        this.$store.commit('session/setToken', token);
        await this.$store.dispatch('ficha/reloadAll');
        await this.$store.dispatch('session/useIntervalValidator');
        await this.$router.push('/');
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
        this.$store.commit('session/setToken', null);
        if (!(e && e.response && e.response.data && e.response.data.mensagem)) {
          this.$alert({text: 'Falha no login', type: 'error'});
        }
        else if (e.response.status === 412) {
          this.emailAtivacao = e.response.data.dados;
          this.dialogEmailAtivacao = true;
        }
      } finally {
        this.loadingLogin = false;
        this.disableForms = false;
      }
    },
    async submitFirstAccess() {
      if (!this.$refs['form-firstaccess'].validate()) return;
      this.loadingFirstAccess = true;
      this.disableForms = true;
      try {
        const appWebClient = new AppWebClient();
        const msg = await appWebClient.insertUsuario(this.iptCpf, this.iptRegional, this.iptEmail, this.iptPassword);
        if (msg) {
          this.$alert({text: msg, type: 'success', timeout: 3000});
          this.dialogFirstAccess = false;
        }
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      } finally {
        this.loadingFirstAccess = false;
        this.disableForms = false;
      }
    },
    async reenviarEmail() {
      this.loadingReenviarEmail = true;
      try {
        const appWebClient = new AppWebClient();
        const msg = await appWebClient.reenviarConfirmacaoDeConta(this.iptCpf, this.iptRegional);
        if (msg) {
          this.$alert({text: msg, type: 'success', timeout: 3000});
          this.dialogEmailAtivacao = false;
        }
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      } finally {
        this.loadingReenviarEmail = false;
      }
    },
    async submitRequisitarRecuperacaoDeSenha() {
      if (!this.$refs['form-passwdrequest'].validate()) return;
      this.loadingPasswdRecover = true;
      const appWebClient = new AppWebClient();
      try {
        await appWebClient.requestPasswdRecover(StringHelper.extractNumbers(this.iptCpf), this.iptEmail, this.iptRegional);
        this.stepperPasswdRecover = 2;
      } finally {
        this.loadingPasswdRecover = false;
      }
    },
    async submitConferirCodigoDeRecuperacao() {
      if (!this.$refs['form-passwdcode'].validate()) return;
      this.loadingPasswdRecover = true;
      const appWebClient = new AppWebClient();
      try {
        const codigoNaoExpirado = await appWebClient.checkPasswdCode(StringHelper.extractNumbers(this.iptCpf), this.iptRegional, this.iptResetCode);
        if (codigoNaoExpirado === false) {
          this.$alert({text: 'O cdigo informado est vencido', type: 'error', timeout: 5000});
          this.stepperPasswdRecover = 1;
        }
        else this.stepperPasswdRecover = 3;
      } finally {
        this.loadingPasswdRecover = false;
      }
    },
    async submitAlterarSenha() {
      if (!this.$refs['form-newpasswd'].validate()) return;
      this.loadingPasswdRecover = true;
      const appWebClient = new AppWebClient();
      try {
        const codigoNaoExpirado = await appWebClient.changePasswd(StringHelper.extractNumbers(this.iptCpf), this.iptRegional, this.iptResetCode, this.iptPassword);
        if (codigoNaoExpirado === false) {
          this.$alert({text: 'O tempo do cdigo de segurana expirou', type: 'error', timeout: 5000});
          this.stepperPasswdRecover = 1;
        }
        else {
          this.$alert({text: 'Senha alterada', type: 'success'});
          this.dialogPasswdRecover = false;
        }
      } finally {
        this.loadingPasswdRecover = false;
      }
    },
    rulePasswordRepeat(v) {
      return v === this.iptPassword || 'As senhas no esto iguais'
    },
  },
  watch: {
    dialogFirstAccess(v) {
      if (v === false) {
        this.iptPassword = '';
        this.iptPasswordRepeat = '';
        this.$refs['form-firstaccess'].resetValidation();
      }
    }
  }
}
</script>
